import React from 'react'
import classes from './About.module.css'

export const About = ({ about }) => {
  return (
    <main className={classes.about}>
      <div>
        <h1 className={classes.head}>О нас:</h1>
        <div>
          <p>
            <img className={classes.image} src={about.img} alt={about.alt} />
            {about.text}
          </p>
        </div>
      </div>
    </main>
  )
}
